* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-style: normal;
}

.product_card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding-left: 16px;
  padding-right: 16px;
}

.product_card_imge {
  text-align: center;
  width: 160px;
  height: 134px;
  margin-top: 32px;
  margin-left: 3px;
  display: flex;
}

.product_card-name {
  text-align: left;
  color: #060b35;
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product_card-code {
  text-align: left;
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars {
  display: inline-flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  display: inline-block;
}

.stars :not(:last-child) {
  margin-right: 4px;
}

.stars--4 :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.product_card-reviews {
  text-align: right;
  color: #060b35;
  padding-top: 2px;
