  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product_card-price-value {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product_card-button {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  margin-top: 16px;
  margin-right: 1px;
  padding: 12px 67px;
  font-size: 14px;
  font-weight: 700;
